body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #0E0B16;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-color: rgba(41, 61, 90);
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.navigation {
  position: fixed;
  top: 0;
  width: 100%;
  background-color: #0E0B16;
}

.navigation .nav-links {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-around;
            justify-content: space-around;
    width: 40%;
    margin-left: 60%;
    font-family: "SF Mono", "Fira Code", "Fira Mono", "Roboto Mono", "Lucida Console", Monaco, monospace;
    font-size: 20px;
    margin-top: 2%;

}

.navigation .nav-links a {
    color: lightgray;
}

.navigation .nav-links a:hover {
    color: #A239CA;
}

.about {
    width: 40%;
    margin: auto;
}
.about h1 {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 4em;
    color: #E7DFDD;
    margin-bottom: -15%;
    margin-top: 20%;
}

.about h6{
    color: #E7DFDD;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 3em;
    margin-bottom: 10%;
}

.about p {
    color: #E7DFDD;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 20px;
}

.about .skills {
    display: -webkit-flex;
    display: flex;
    width: 55%;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    color: #A239CA;
    font-family: "SF Mono", "Fira Code", "Fira Mono", "Roboto Mono", "Lucida Console", Monaco, monospace;
}
.projects {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    width: 70%;
    margin: auto;
}

.projects .project-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.projects .project-container h3 {
    color: #E7DFDD;
    font-size: 22px;
    margin-top: 10%;
}

.projects .project-container img {
    width: 60%;
}

.projects .project-container p {
    width: 60%;
    color: #E7DFDD;
}

.projects .project-container .project-links {
    display: -webkit-flex;
    display: flex;
    width: 20%;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    margin-top: 2%;
    margin-bottom: 2%;
}

.projects .project-container .fa-desktop:hover {
    color: #A239CA;
}

.projects .project-container .fa-code:hover {
    color: #A239CA;
}
.contact {
    margin: auto;
    margin-top: 10%;
    width: 50%;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.contact h1 {
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 3.4em;
    color: #E7DFDD;
}

.contact p {
    color: #E7DFDD;
    font-family: 'Gill Sans', 'Gill Sans MT', Calibri, 'Trebuchet MS', sans-serif;
    font-size: 1.2em;
    margin-bottom: 5%;
}

.contact .contact-links {
    display: -webkit-flex;
    display: flex;
    margin-top: 2%;
    width: 50%;
    -webkit-justify-content: space-around;
            justify-content: space-around;

}
.contact .contact-links .fa-envelope:hover {
    color: #A239CA;
}

.contact .contact-links .fa-github:hover {
    color: #A239CA;
}

.contact .contact-links .fa-linkedin:hover {
    color: #A239CA;
}

